/*--/testimonials--*/
.owl-testimonial-page .blockquote {
    position: relative;
    padding-left: 75px;
    font-size: 18px;
    font-style: italic;
    font-weight: 500;
  }
  
  .owl-testimonial-page .icon-quote {
    position: absolute;
    top: 0px;
    left: 0px;
  }
  
  .owl-testimonial-page .icon-quote span.fa {
    font-size: 60px;
    color: var(--quote-color);
    opacity: 0.1;
    transition: 0.3s;
  }
  
  .owl-testimonial-page .side-title {
    font-size: 22px;
    line-height: 28px;
    font-weight: 600;
  }

.owl-testimonial-page.owl-carousel.owl-theme .owl-dots.disabled,
.owl-testimonial-page.owl-carousel.owl-theme .owl-nav.disabled {
  display: none;
}

.owl-testimonial-page.owl-theme .owl-nav [class*="owl-"] {
  color: var(--navigation-dot);
}
.owl-testimonial-page.owl-theme .owl-nav [class*="owl-"]:hover{
  opacity: 1;
  color: var(--primary-color);
}
.owl-testimonial-page.owl-carousel.owl-theme .owl-dots .owl-dot.active span,
.owl-testimonial-page.owl-carousel.owl-theme .owl-dots .owl-dot:hover span {
  background-color: var(--primary-color);
  opacity: 1;
}
.owl-testimonial-page .owl-dots {
  margin-top: 0px;
  position: absolute;
  right: 0px;
  left: 0px;
  bottom: 30px;
  background: none;
}

.owl-testimonial-page.owl-carousel.owl-theme .owl-dots .owl-dot span {
  width: 16px;
  height: 16px;
  margin: 5px 2px;
  background: var(--secondary-color);
  display: block;
  -webkit-backface-visibility: visible;
  transition: opacity 0.2s ease;
  border-radius: 50%;
  position: relative;
  opacity: 0.4;
}

[data-theme="dark"] .owl-testimonial-page.owl-carousel.owl-theme .owl-dots .owl-dot span {
  background: var(--navigation-dot);
}

.owl-testimonial-page.owl-carousel.owl-theme .owl-dots .owl-dot.active span,
.owl-testimonial-page.owl-carousel.owl-theme .owl-dots .owl-dot:hover span {
  background: var(--primary-color);
  opacity: 1;
}

/*--//testimonials--*/